var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test('success open', function (t) {
  return request.post('/')
    .send({
      "host": "113.172.82.132",
      "user": "support",
      "pass": "support"
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connection success');
    });
});
